<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }


</style>

<template>
    <div class="pages">
        <H3>通知详情</H3>
        <div class="line"></div>
        <el-form class="form" :rules="roles" ref="form" :model="form" label-width="120px">
            <el-form-item label="标题">
                <el-input style="width: 300px" v-model="form.title" placeholder="请输入"></el-input>

                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
            <rich-view v-model="form.rich_text"></rich-view>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/information'

    export default {

        name: "MessageAdd",
        data() {
            return {
                form: {
                    title: "",
                },
                api: null,
                isNoEdit: true,
                isLoading: false,
                roles: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        {min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur'}
                    ],
                }
            }
        },
        mounted() {
            this.init();
        },
        methods: {

            init() {
                if (this.$route.query.id !== undefined) {
                    this.isNoEdit = true;
                    this.loadData()
                } else {
                    this.isNoEdit = false;
                }
            },

            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.title)) {
                    this.$message.error('请输入标题');
                    return;
                }

                if (!api.isStrValid(this.form.rich_text)) {
                    this.$message.error('请输入富文本详情');
                    return;
                }
                this.isLoading = true;
                api.save(this.form, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'information'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })

            },
            back() {
                this.$router.back();
            },

            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },

        }
    }
</script>
